.services {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: min-content;
  row-gap: 10px;
  font-weight: 700;
  padding-top: 10px;
}

.services__label {
  padding-left: 25px;
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &::before {
    left: 0;
    width: 18px;
    height: 18px;
    box-sizing: border-box;
    border: 2px solid currentColor;
    border-radius: 2px;
  }

  &::after {
    display: none;
    left: 5px;
    width: 0;
    height: 0;
    border: 4px solid currentColor;
    border-radius: 50%;
  }

  &:nth-of-type(5n+3),
  &:nth-of-type(5n+5) {
    padding-left: 45px;

    &::before {
      left: 20px;
    }

    &::after {
      left: 25px;
    }
  }

  input[type="checkbox"]:checked + &::after {
    display: block;
  }

  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}
