.client-requests {
  width: 100%;
  min-width: 1215px;
  margin-bottom: 120px;
  border-collapse: collapse;
  border-radius: 10px;

  & > * {
    margin: 0;
    padding: 0;
    text-align: center;
    box-sizing: border-box;
  }

  th:not(:last-child),
  td:not(:last-child) {
    border-right: 1px dashed $main-color;
  }

  th,
  td {
    width: min-content;
    padding-left: 2px;
    padding-right: 2px;
    border-bottom: 1px solid $main-color;
  }

  tr:nth-child(2n) {
    background-color: $table-even-row-bg-color;
  }

  button[data-btn="delete"] {
    border-radius: 5px;

    @include state-set-aux;

    &:active {
      background-color: $warning-color;
      transition-duration: 0.01s;
    }
  }

  .email {
    font-size: 16px;
    line-height: 1;
  }
}
