.booking {
  width: 230px;

  @media (max-width: $pre-desktop-width) {
    width: 80%;
    margin-top: 0;
    margin-bottom: 20px;
    padding-top: 15px;
  }

  @media (max-width: $pre-tablet-width) {
    width: 100%;
  }
}

.booking__heading {
  width: 80%;
  margin: 0 auto;
  padding: 0;
  margin-bottom: 10px;
  font-size: 21px;
  line-height: 1.2;
  font-weight: 500;
  text-align: center;

  @media (max-width: $pre-desktop-width) {
    font-size: 23px;
    font-weight: 700;
    letter-spacing: 1.1px;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  @media (max-width: $pre-tablet-width) {
    font-size: 18px;
    line-height: 1.1;
    margin-bottom: 10px;
  }
}

.booking__form {
  @include column-flex;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 15px;
  border-radius: 15px;
  box-shadow: none;
  color: $main-form-color;
  transform: scale(1);
  transition:
    transform 0.4s ease-in,
    box-shadow 0.8s ease-in;

  & > * {
    margin-left: 0;
    margin-right: 0;
    padding: 0;
    width: 100%;
  }

  &--illuminated {
    transform: scale(1.01);
    box-shadow: 0 0 20px $aux-color;
  }
}

.booking__box {
  @include casual-flex;

  @media (max-width: $pre-desktop-width) {
    flex-direction: column;
  }
}

.booking__label {
  @include casual-flex;
  flex-basis: 100px;
  flex-grow: 1;

  & > * {
    flex-basis: 150px;
    flex-grow: 1;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;

    @media (min-width: $desktop-width) {
      max-width: 200px;
    }
  }

  @media (max-width: $pre-desktop-width) {
    width: 100%;
    font-size: 26px;
    line-height: normal;
    max-height: 70px;
  }

  @media (max-width: $pre-tablet-width) {
    font-size: 20px;
  }

  span {
    @media (min-width: $desktop-width) {
      font-size: 18px;
    }

    @media (max-width: $pre-desktop-width) {
      flex-grow: 0;
      flex-shrink: 1;
    }
  }
}

.booking__input {
  height: 20px;
  margin-bottom: 15px;
  padding-bottom: 3px;
  background-color: transparent;
  border-bottom: 1px solid $main-color;
  text-align: center;
  opacity: 0.5;

  @media (max-width: $pre-desktop-width) {
    margin-bottom: 5px;
    padding-bottom: 5px;
  }

  &:hover,
  &:focus {
    opacity: 1;
  }
}

.booking__submit {
  @include general-btn;
  color: currentColor;
  font-size: 18px;
  line-height: 34px;
  margin: 25px auto 5px;
  background-color: transparent;
  border: 1px solid $main-color;

  &:hover {
    transform: scale(1);
  }

  &:hover,
  &:focus {
    box-shadow: 0 0 10px $aux-color;
  }
}
