.map {
  width: 100%;
  margin-bottom: 30px;

  @media (max-width: $pre-desktop-width) {
    margin-bottom: 90px;
  }
}

.map__farme {
  width: 100%;
  height: 50vh;
  border-radius: 10px;
  background-color: $map-bg-color;
  background-image: $map-bg;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
