.header {
  display: flex;
  align-items: center;
  padding-top: 30px;
  font-weight: 500;

  @media (max-width: $pre-desktop-width) {
    margin-bottom: 50px;
  }

  @media (max-width: $pre-tablet-width) {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
  }

  a {
    @media (max-width: $pre-tablet-width) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

.header__phone {
  @include underline;
  letter-spacing: 0.1rem;
  margin-left: auto;
  margin-right: 300px;

  @media (max-width: $pre-desktop-width) {
    margin-right: 0;
  }

  @media (max-width: $pre-tablet-width) {
    margin-left: 0;
    letter-spacing: normal;
  }
}
