.main {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows:
    min-content
    1fr
    min-content;
  grid-template-areas:
    "h b"
    "c b"
    "m b";

  @media (max-width: $pre-desktop-width) {
    @include column-flex;
  }
}

.main__aux-header {
  grid-area: h;

  @media (max-width: $pre-desktop-width) {
    @include column-flex;
  }
}

.main__leading-heading {
  font-size: 100px;
  line-height: 1.2;
  margin: 0 0 0 6%;
  padding: 75px 0 90px;

  background-image: $doodle;
  background-size: 600px 640px;
  background-repeat: no-repeat;
  background-position: 140px -168px;

  @media (max-width: $pre-desktop-width) {
    @include column-flex;
    margin-left: 0;
    width: 100%;
    text-align: center;
    background-position: 60% -158px;
  }

  @media (max-width: $pre-tablet-width) {
    font-size: 55px;
    padding: 65px 0;
    margin-bottom: 30px;
    background-size: 460px 500px;
    background-position: center -135px;
  }

  sub {
    font-size: 16px;
    line-height: 28px;
    font-style: italic;
    font-weight: 500;

    @media (max-width: $pre-desktop-width) {
      margin-left: -15px;
    }

    @media (max-width: $pre-tablet-width) {
      font-weight: 400;
    }
  }
}

.main__leading-paragraph {
  width: 85%;
  line-height: 30px;

  @media (max-width: $pre-desktop-width) {
    width: 88%;
    margin-top: 0;
    text-align: center;
  }

  @media (max-width: $pre-tablet-width) {
    width: 100%;
    font-weight: 500;
  }
}

.main__book-btn {
  @include general-btn;
  margin-bottom: 50px;

  @media (max-width: $pre-desktop-width) {
    margin-top: 10px;
    margin-bottom: 100px;
  }

  @media (max-width: $pre-tablet-width) {
    margin-bottom: 70px;
  }
}

.main__booking {
  grid-area: b;
}

.main__contacts {
  grid-area: c;
}

.main__map {
  grid-area: m;
}
