.contacts {
  width: 100%;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    font-size: 22px;
  }
}

.contacts__address {
  @include casual-flex;
  width: 85%;

  @media (max-width: $pre-tablet-width) {
    font-size: 18px;
    line-height: 1.8;
  }

  & > * {
    width: max-content;

    @media (max-width: $pre-tablet-width) {
      margin-left: 10px;
    }
  }

  a {
    @include underline;

    @media (min-width: $tablet-width) {
      margin-left: auto;
    }

    @media (max-width: $pre-tablet-width) {
      margin-right: auto;
    }
  }
}

.contacts__location {
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    margin-left: 0;
  }

  @media (max-width: $pre-tablet-width) {
    margin-right: auto;
  }
}

.contacts__span {
  font-weight: 500;
}
