.error,
.success {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: flex-start;

  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  background-color: $message-window-bg-color;
}

.error__inner,
.success__inner {
  width: 30%;
  min-width: 290px;
  box-sizing: border-box;
  padding: 70px 20px 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  text-align: center;
  font-weight: 700;
  font-size: 20px;
  color: $main-color;

  background-color: $message-bg-color;
  border-radius: 10px;

  @media (max-width: $pre-desktop-width) {
    font-size: 18px;
    padding-top: 50px;
    padding-bottom: 30px;
  }

  @media (max-width: $pre-tablet-width) {
    font-size: 16px;
  }
}

.error__title,
.success__title {
  margin: 0;
  margin-bottom: 50px;

  @media (max-width: $pre-desktop-width) {
    margin-bottom: 30px;
  }
}

.error__button,
.success__button {
  @include general-btn;
  background-color: $aux-color;
  color: $main-color;
  border: 3px solid currentColor;
  margin-top: 0;
  margin-bottom: 0;
  text-transform: none;

  &:hover,
  &:focus {
    color: $aux-color;
    background-color: $main-color;
    border-color: transparent;
    transform: translate(0, 0);
  }

  &:active {
    transform: scale(1.08);
  }
}
