//blogger sans regular
@font-face {
  font-family: "Blogger Sans";
  font-style: normal;
  font-weight: 400;
  src:
    local("Blogger Sans"),
    local("bloggerSans-regular"),
    url("../fonts/bloggersans.woff2") format("woff2"),
    url("../fonts/bloggersans.woff") format("woff");
  font-display: swap;
}

