@mixin casual-flex {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

@mixin column-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin centering-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin centering-grid {
  display: grid;
  place-items: center;
}

@mixin general-btn {
  width: min-content;
  padding: 10px 40px;
  margin: 35px 0 65px;
  border-radius: 10px;
  color: $aux-color;
  font-weight: 500;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  background-color: $main-color;

  &:hover {
    transform: scale(1.05) translate3d(20px, 0, 0);

    @media (max-width: $pre-desktop-width) {
      transform: scale(1.05);
    }
  }

  &:focus {
    box-shadow: 0 0 10px $aux-color;
  }

  &:active {
    color: $main-color;
    background-color: $aux-color;
  }
}

@mixin underline {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.1em;
    background-color: currentColor;

    opacity: 1;
    transform: scale(0);
    transform-origin: center;
    transition-duration: 0.3s;
  }

  &:hover::after,
  &:focus::after {
    transform: scale(1);
  }
}

@mixin state-set-aux {
  &:hover,
  &:focus {
    color: $aux-color;
    background-color: $main-color;
  }
}
