.login-form {
  width: 300px;
  height: min-content;
  padding: 20px 30px;
  background-color: $login-form-bg-color;
  border-radius: 15px;
  box-shadow: 0 5px 15px $login-form-shadow-color;
}

.login-form__box {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.login-form__label {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.login-form__label > * {
  width: 100%;
  flex-grow: 1;
}

.login-form__input {
  text-align: center;
  outline: none;
  border: none;
  border-bottom: 1px solid $login-form-border-color;
  background-color: transparent;
}

.login-form__submit {
  align-self: center;
  width: max-content;
  padding: 15px;
  border-radius: 10px;
  background-color: $aux-color;
  cursor: pointer;
}
