.nav {
  @media (max-width: $pre-tablet-width) {
    margin-bottom: 10px;
  }
}

.nav__list {
  display: flex;
  margin-right: 80px;

  @media (max-width: $pre-tablet-width) {
    flex-direction: column;
    margin-right: 0;
  }
}

.nav__item {
  &:not(:first-child) {
    margin-left: 30px;

    @media (max-width: $pre-tablet-width) {
      margin-left: 0;
    }
  }

  &:not(:last-child) {
    @media (max-width: $pre-tablet-width) {
      margin-bottom: 5px;
    }
  }
}

.nav__link {
  box-sizing: border-box;
  width: max-content;
  letter-spacing: 0.05rem;
  padding: 0 10px;
  border: 2px solid transparent;
  border-radius: 10px;

  @media (max-width: $pre-tablet-width) {
    @include underline;
    padding: 0;
    border: none;
    letter-spacing: normal;
  }


  &:hover,
  &:focus {
    @media (min-width: $tablet-width) {
      border-color: $main-color;
    }
  }

  &:active {
    @media (min-width: $tablet-width) {
      color: $aux-color;
      background-color: $main-color;
    }

    @media (max-width: $pre-tablet-width) {
      color: $active-nav-link-color;
    }
  }
}
