.footer {
  border-top: 2px solid $main-color;
  position: relative;
}

.footer__invitation {
  @include casual-flex;
  justify-content: space-between;

  span:nth-child(2) {
    font-size: 35px;
    line-height: 1.4;
    font-weight: 700;
  }
}

.footer__gratitude {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-auto-rows: min-content;
  width: min-content;
  margin-left: auto;

  font-size: 16px;
  line-height: 1.15;
  letter-spacing: -0.8px;

  @media (max-width: $pre-tablet-width) {
    font-size: 12px;
    margin-left: 0;
    margin-right: auto;
  }

  & > * {
    margin: 0;
    padding: 0;
  }

  dd {
    padding-left: 15px;
  }

  a {
    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }
}

.footer__to-top-btn {
  display: grid;
  place-items: center;
  position: absolute;
  bottom: 180px;
  right: 0;

  width: 46px;
  height: 46px;
  padding: 0;
  border-radius: 50%;
  background-color: $aux-color;

  @media (max-width: $pre-desktop-width) {
    bottom: 160px;
  }

  @media (max-width: $pre-tablet-width) {
    bottom: 175px;
  }

  &::before {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    box-sizing: border-box;
    border-top: 3px solid currentColor;
    border-left: 3px solid currentColor;
    transform: translate(0, 2px) rotate(45deg);
  }

  &--admin {
    bottom: 160px;
  }

  @include state-set-aux;
}
