.page {
  width: 100%;
  min-width: $mobile-width;
  background-color: $gradient-color-1;
  background-image: linear-gradient(to right, $gradient-color-1 60%, $gradient-color-2);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  font-family:
    "Blogger Sans",
    "Arial",
    sans-serif;
  font-size: 20px;
  line-height: 34px;
  font-weight: 400;
  color: $main-color;

  @media (max-width: $pre-tablet-width) {
    font-size: 16px;
    line-height: 1.4;
  }

  &--login {
    @include centering-flex;
    min-height: 100vh;
  }
}

.modal-open {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: 90%;
  max-width: 1215px;
  min-height: 100vh;
}

.main-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows:
    min-content
    1fr
    min-content;

  @media (max-width: $pre-desktop-width) {
    @include column-flex;

    & > * {
      width: 100%;
    }
  }

  &--admin {
    grid-template-columns: 1fr;
    grid-template-areas:
      "h"
      "m"
      "f";
  }
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}

a {
  display: block;
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
}

a,
button,
label {
  transition: all 0.2s ease;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

fieldset,
input,
button {
  border: none;
  outline: none;
}
